.intro {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  padding: 11.5rem 0 10rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 115px 0 100px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    padding: 5rem 0 5.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 16vh 0;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 16vh 0 5vh;
  }
}

.intro__title {
  margin: 0;
  max-width: 100rem;
  text-transform: uppercase;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 12rem;
  line-height: 0.906;
  letter-spacing: -0.04em;

  .text__word {
    display: inline-block;
    margin: 0;
    overflow: hidden;
    line-height: 1.6;

    span{
      transform: translate3d(0, 1.5em, 0);
      display: inline-block;
     }
  }

  .text__word:nth-of-type(2){
    transform: translateY(-57%)
  }

  &.active{

    .text__word {
      span{
        transform: translate3d(0, 0, 0);
       }
    }

  }
    
   

  @media (min-width: $stop-scaling) {
    max-width: 998px;
    font-size: 120px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    max-width: 100%;
    font-size: 3.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    font-size: 10rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 5rem;
  }
}

.intro__content {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-left: 6.3rem;

  @media (min-width: $stop-scaling) {
    padding-left: 63px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    justify-content: flex-start;
    padding-left: 2.5rem;
  }
}

.intro__info {
  flex: none;
  width: 50rem;
  max-width: 50%;

  @media (min-width: $stop-scaling) {
    width: 500px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }
}

.intro__message {
  flex: none;
  width: 50.9rem;
  max-width: 50%;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.27;
  animation:  ease-in 0.5s fadeIn;

  @keyframes fadeIn {
    0% {
      opacity: 0;
      transform: translatey(20%);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }

  @media (min-width: $stop-scaling) {
    width: 509px;
    font-size: 24px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }

  p {
    margin: 0;
  }

  svg {
    @media (min-width: $stop-scaling) {
      width: 16px;
      height: 16px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      width: 1.1rem;
      height: 1.1rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 1.1rem;
      height: 1.1rem;
    }
  }
}

.intro__label {
  margin: 0 0 1.6rem;
  max-width: 25rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;

  @media (min-width: $stop-scaling) {
    margin: 0 0 16px;
    width: 250px;
    font-size: 24px;
  }
}

.intro__date {
  margin: 0 0 0 -0.5rem;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;

  @media (min-width: $stop-scaling) {
    margin: 0 0 0 -5px;
    font-size: 48px;
  }
}
